export const PopUpAction = styled.li`
    padding: .625rem 1.25rem;
    width: calc(100% - 2 * (1.25rem) );
    border-radius: 1.25rem;
    background-color: ${props => {
        if (props.actionId % 2 === 0) {
            return "#5142FC";
        } else {
            return "transparent";
        }
    }};
    color: ${props => {
        if (props.actionId % 2 === 0) {
            return "white";
        } else {
            return "#5142FC";
        }
    }};

    border: solid .0625rem #5142FC;
    text-align: center;
    font-size: .9375rem;
    font-weight: 700;

    cursor: pointer;

    &:hover {
        opacity: 80%;
    }
`;  