import styled from "styled-components";
import { DARK_MODE, LIGHT_MODE } from "../../stateManagement/redux/reducers/UIUXReducer";

export const AppOverviewContainer = styled.div`

    padding-left: 1rem;
    padding-right: 1rem;
    width: calc(100% - 2rem);
    height: 100vh;
    margin-top: auto;
    margin-bottom: auto;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;

    & > * {
        margin-left: auto;
        margin-right: auto;
    }
`;

export const OverviewTitle = styled.h1`
    @keyframes fromDownToUp1 {
        0% {
            transform: translateY(500%);
            opacity: 0%;
        }

        100% {
            transform: translateY(0%);
            opacity: 100%;
        }
    }
    
    display: flex;
    color: white;
    flex-direction: column;
    font-size: 3.375rem;
    line-height: 4.25rem;
    width: fit-content;
    margin-top: auto;
    margin-bottom: 0;
    transition: all .25s linear 0s;
    text-align: center;

    &.animated {
        animation: fromDownToUp1 1.5s ease-in-out 1;
    };

    & > * {
        margin-left: auto;
        margin-right: auto;
    }

    & .tokensWord {
        -webkit-text-stroke-width: .0625rem;
        -webkit-text-stroke-color: ${props => {
            switch (props.illumMode) {
                case DARK_MODE:
                    return "black";
                case LIGHT_MODE:
                default:
                    return "#fff";

            }
        }};
        -webkit-text-fill-color: ${props => {
            switch (props.illumMode) {
                case DARK_MODE:
                    return "black";
                case LIGHT_MODE:
                default:
                    return "rgb(0 0 0/0%)";

            }
        }};
        
        color: ${props => {
            switch (props.illumMode) {
                case DARK_MODE:
                    return "black";
                case LIGHT_MODE:
                default:
                    return "rgb(0 0 0/0%)";

            }
        }};
        background: ${props => {
            switch (props.illumMode) {
                case DARK_MODE:
                    return "linear-gradient(-45deg, #E250E5, #4B50E6, #E250E5, #4B50E6)";
                case LIGHT_MODE:
                default:
                    return "rgb(0 0 0/0%)";

            }
        }};
        -webkit-background-clip: text;
        -webkit-text-stroke: ${props => {
            switch (props.illumMode) {
                case DARK_MODE:
                    return "3px transparent";
                case LIGHT_MODE:
                default:
                    return "visible";

            }
        }};
    }
`;

export const OverviewContent = styled.p`

    @keyframes fromDownToUp2 {
        0% {
            transform: translateY(3000%);
            opacity: 0%;
        }

        100% {
            transform: translateY(0%);
            opacity: 100%;
        }
    }

    &.animated {
        animation: fromDownToUp2 2s ease-in-out 1;
    };

    line-height: 1.875rem;
    font-size: 1.25rem;
    color: #EBEBEB;
    text-align: center;
`