import styled from "styled-components";
import { DARK_MODE, HEADER_FIXED, HEADER_UNFIXED, LIGHT_MODE } from "../../stateManagement/redux/reducers/UIUXReducer";

export const HeaderContainer = styled.div`

    @keyframes dropHeader {
        0% {
            margin-top: calc(-2rem - 2rem - 0.75rem - 0.75rem - 1.25rem);
        }

        100% {
            margin-top: 0;
        }
    };
    
    @keyframes pullUpHeader {
        0% {
            margin-top: 0;
            background: ${props => {
                switch (props.illumMode) {
                    case DARK_MODE:
                        return "#14141F";
                    case LIGHT_MODE:
                    default:
                        return "linear-gradient( 227.3deg, #8A208C 0%, #181B81 100.84%)";
                }
            }};
        }

        100% {
            margin-top: -100%;
            background: linear-gradient( 227.3deg, #8A208C 0%, #181B81 100.84%);
            background: ${props => {
                switch (props.illumMode) {
                    case DARK_MODE:
                        return "#14141F";
                    case LIGHT_MODE:
                    default:
                        return "linear-gradient( 227.3deg, #8A208C 0%, #181B81 100.84%)";
                }
            }};
        }
    };
    display: flex;
    width: 100%;
    border-bottom: .0625rem solid rgba(235, 235, 235, 0.2);
    left: 0;
    top: 0;
    overflow-y: visible;
    background: transparent;
    transition: 0;
    z-index: 1000000;

    margin-top: ${props => {
        switch (props.headerPosition) {
            case HEADER_FIXED:
                return "-100%";
            case HEADER_UNFIXED:
            default:
                return "0";
        }
    }};

    position: ${props => {
        switch (props.headerPosition) {
            case HEADER_FIXED:
                return "fixed";
            case HEADER_UNFIXED:
            default:
                return "absolute";
        }
    }};

    &.pulledUp {
        animation: pullUpHeader .5s ease-in-out 1;
        
        transition: all .5s linear .25s;
    }   

    &.pulledUp .illumModeToolsContainer {
        opacity: 0%;
        position: absolute;
        z-index: -1000000000000;
    }

    &.slidedDown {
        animation: dropHeader .25s linear 1;
        
        background: ${props => {
            switch (props.illumMode) {
                case LIGHT_MODE:
                    return "linear-gradient( 227.3deg, #8A208C 0%, #181B81 100.84%)";
                case DARK_MODE:
                default:
                    return "#14141F";
            }
        }};

        border-bottom: none;
        box-shadow:  1px 1px 5px 0px rgb(0 0 0 / 50%);


        margin-top: 0;
    }

    &.slidedDown .illumModeToolsContainer {
        opacity: 0%;
        position: absolute;
        z-index: -1000000000000;
    }

    & .limiter {
        margin-left: auto;
        margin-right: auto;
        display: flex;
        padding: 1rem;
        width: 100%;
        max-width: 1500px;
    }

    & .limiter > * {
        flex: 1;
    }

    & #title {
        cursor: pointer;
        color: white;
        height: fit-content;
        width: fit-content;
        font-weight: 700!important;
        font-size: 1.25rem;
    }

    & .searchBarContainer {
        padding: 0;
        position: absolute;
        height: 0;
        width: fit-content;
        bottom: 0;
        right: calc(1rem + 2.5rem + .5rem + 2.5rem + .5rem + 12.49375rem + 5rem);
    }

    @media screen and (max-width: 1000px) {
        & .searchBarContainer {
            right: calc(1rem + 2.5rem + 5rem);
        }
    }

    @media screen and (max-width: 700px) {
        & .searchBarContainer {
            /* right: 15rem; */
        }
    }

    @media screen and (max-width: 500px) {
        & .searchBarContainer {
            /* right: 1rem; */
        }
    }
`