import styled from "styled-components";

export const SearchForm =styled.form`
    display: flex;
    background: #F8F8F8;
    border: .0625rem solid #fff;
    border-radius: 1.875rem;
    overflow: hidden;
    /* padding: .8125rem .9375rem; */
    padding-left: .9375rem;
    padding-right: .9375rem;

    visibility: ${props => {
        if (props.show) {
            return "visible";
        }

        return "hidden";
    }};
    position: ${props => {
        if (props.show) {
            return "relative";
        }

        return "absolute";
    }};

    & > * {
        margin-top: auto;
        margin-bottom: auto;
    }

    & .searchField {
        border: none;
        padding-top: .8125rem;
        padding-bottom: .8125rem;
        background: #F8F8F8;
        height: calc(100% + 2 * .8125rem);
        color: black;
    }

    & .searchField:focus {
        outline: none;
    }

    & .searchBtnFrame {
        padding: 0;
        border: none;
        background: #F8F8F8;
        height: 1.25rem;
        width: 1.25rem;
        margin-left: .5rem;

        cursor: pointer;
    }

    & .searchBtnFrame > * {
        width: 100%;
        height: 100%;
    }
`;