import { useState } from "react";
import { SubscriptionForm } from "./Style";
import  { ReactComponent as MsgIcon } from "../../assets/svgs/PaperMsg.svg";
import { useSelector } from "react-redux";

const MailSubscriptionForm = () => {

    const illumMode = useSelector(state => state.uiux.illumMode);

    const [email, setEmail] = useState("");

    return (
        <SubscriptionForm type="post"  illumMode={illumMode} >
            <input
                className="emailField"
                placeholder="name@email.com"
                required
                value={email}
                onChange={e => {
                    setEmail(e.target.value);
                }}
            />
            <button
                className="subscribeBtn"
                type="submit"
                onClick={e => {
                    if (email !== "") {
                        e.preventDefault();

                        //submit email-address to remote repo
                    }
                }}
            ><MsgIcon /></button>
        </SubscriptionForm>
    );
}
 
export default MailSubscriptionForm;