import { FIX_HEADER, SET_DARK_MODE, SET_LIGHT_MODE, SET_VIEWPORT_LAYOUT, TOGGLE_SEARCH_BAR, UNFIX_HEADER } from "./actionTypes"


export const setViewportLayout = (layout) => {
    return {
        type: SET_VIEWPORT_LAYOUT,
        layout: layout
    };
}

export const setDarkMode = () => {
    return {
        type: SET_DARK_MODE
    }
}

export const setLightMode = () => {
    return {
        type: SET_LIGHT_MODE
    }
}

/**
 * fixes the header to the top of the viewpoint
 * @returns 
 */
export const fixHeader = () => {
    return {
        type: FIX_HEADER
    }
}

/**
 * returns the header positioning back to being relative to document
 * @returns 
 */
export const unfixHeader = () => {
    return {
        type: UNFIX_HEADER
    }
}

/**
 * 
 * @param {*} header 
 * @param {*} delayTillSlide in seconds
 */
export const fixHeaderThenSlideDown = (header, delayTillSlide) => {

    return (dispatch) => {

        dispatch(fixHeader());

        setTimeout(() => {
            header.classList.add("slidedDown");
            header.classList.remove("pulledUp");


        }, delayTillSlide * 1000);

    };
}

/**
 * 
 * @param {*} delayTillFix in seconds
 * @returns 
 */
export const pullUpHeaderThenUnfix = (header, delayTillFix) => {

    return (dispatch) => {
        header.classList.add("pulledUp");
        header.classList.remove("slidedDown");


        setTimeout(() => {
            header.classList.remove("pulledUp");
            header.classList.remove("slidedDown");

            dispatch(unfixHeader());

        }, delayTillFix * 1000);
    };
}

export const toggleSearchBar = () => {
    return {
        type: TOGGLE_SEARCH_BAR
    }
}