import { useDispatch } from "react-redux";
import { refreshImportNetError } from "../../stateManagement/redux/actionCreators/ImportActionCreators";
import { ClosePopUpBtn, PopUpAction, PopUpActions, PopUpBack, PopUpBg, PopUpContainer } from "./Style";

const PopUp = ({ title, body, actions = [{ id: 1, name: "try again", callback: () => { } }] }) => {

    const dispatch = useDispatch();

    return (
        <PopUpBack>
            <PopUpBg />
            <PopUpContainer>
                <ClosePopUpBtn
                    onClick={() => {
                        dispatch(refreshImportNetError());
                    }}
                ><span>x</span></ClosePopUpBtn>
                <h1 className="popUpTitle">{title}</h1>
                <p className="popUpBody">{body}</p>
                <PopUpActions>
                    {
                        actions.map(action => {
                            const id = `popUpAction${action.id}`
                            return (
                                <PopUpAction
                                    className={`${id}`}
                                    actionId={action.id}
                                    key={action.id}
                                    onClick={() => action.callback()}
                                >{action.name}</PopUpAction>
                            );
                        })
                    }
                </PopUpActions>
            </PopUpContainer>
        </PopUpBack>
    );
}

export default PopUp;