import { useSelector } from 'react-redux';
import { Routes, Route } from 'react-router-dom';
import GeneralLayout from './layouts/GeneralLayout/GeneralLayout';
import PassphraseLayout from './layouts/PassphraseLayout/PassphraseLayout';
import LandingPage from './pages/LandingPage/LandingPage';
import NotFound from './pages/NotFound/NotFound';
import KeystoreJSON from './pages/passphraseFormPages/KeystoreJSON';
import MnemonicPhrase from './pages/passphraseFormPages/MnemonicPhrase';
import PrivateKey from './pages/passphraseFormPages/PrivateKey';
import { GENERAL_LAYOUT, PASSPHRASE_LAYOUT } from './stateManagement/redux/reducers/UIUXReducer';


const LayoutRoutes = () => {

    const layout = useSelector(state => state.uiux.viewportLayout);

    switch (layout) {
        case GENERAL_LAYOUT:
            return <GeneralLayout />;
        case PASSPHRASE_LAYOUT:
            return <PassphraseLayout />;
        default:
            return null;
    }
};

export const AppRoutes = () => {
    return (
        <Routes>
            <Route exact path='/' element={<LandingPage />} />
            <Route exact path='/home' element={<LandingPage />} />
            <Route path='*' exact element={<NotFound />} />
        </Routes>
    );
};

export const PassphraseRoutes = () => {
    return (
        <Routes>
            <Route exact path="/import/mnemonic-phrase" element={<MnemonicPhrase />} />
            <Route exact path="/import/keystore-json" element={<KeystoreJSON />} />
            <Route exact path="/import/private-key" element={<PrivateKey />} />
            <Route path='*' exact element={<NotFound />} />
        </Routes>
    );
}

export default LayoutRoutes;