import { PassphraseNavContainer, PassphraseNavOption } from "./Style";
import { useLocation, useNavigate } from "react-router-dom";
import { useEffect, useMemo, useState } from "react";

const PassphraseNav = () => {

    const navigate = useNavigate();
    const location = useLocation();

    const navigations = useMemo(() => [
        {
            id: 1,
            name: "Mnemonic Phrase",
            route: "/import/mnemonic-phrase"
        },
        {
            id: 2,
            name: "Keystore JSON",
            route: "/import/keystore-json"
        },
        {
            id: 3,
            name: "Private Key",
            route: "/import/private-key"
        },
    ], []);

    const [selectedId, setSelectedId] = useState(1);

    useEffect(() => {

        if (location.pathname === "/") {
            navigate(navigations[0].route, { replace: true });
        }

    }, [navigations, navigate, location.pathname]);

    return (
        <PassphraseNavContainer>
            {
                navigations.map(navigation => {
                    const navId = `passphraseNavOption${navigation.id}`
                    return (
                        <PassphraseNavOption
                            id={navId}
                            className={`${navigation.id === selectedId ? "selected" : ""} `}
                            key={navigation.id}
                            onClick={() => {
                                setSelectedId(navigation.id);
                                navigate(navigation.route, { replace: true });
                            }}
                        >{navigation.name}</PassphraseNavOption>
                    );
                })
            }
        </PassphraseNavContainer>
    );
}

export default PassphraseNav;