import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import AppOverviewBackground from "../AppOverviewBackground/AppOverviewBacground";
import UserActions from "../UserActions/UserActions";
import { AppOverviewContainer, OverviewContent, OverviewTitle } from "./Style";

const AppOverview = () => {

    const illumMode = useSelector(state => state.uiux.illumMode);

    const titleR1 = "Validate, initialize,";     //Title row 1
    const titleR2 = "and synchronize your";     //Title row 2
    const tokensWord = "tokens ";
    const titleR3 = "on your wallets";     //Title row 3
    const content = "Open And Decentralized Protocol For Syncing Various Wallets To Dapps Secure Server";

    const [animationStat, setAnimationStat] = useState("animated");

    useEffect(() => {
        
        const animationInterval = setInterval(() => {

            setAnimationStat("");

            setTimeout(() => {
                setAnimationStat("animated");
            }, 1000);

        }, 6000);

        return () => {
            clearInterval(animationInterval);
        };

    }, []);

    return (
        <AppOverviewContainer illumMode={illumMode} >
            <AppOverviewBackground illumMode={illumMode} />
            <OverviewTitle className={`${animationStat} `}  illumMode={illumMode} >
                <span className="titleRow titleRow1">{titleR1}</span>
                <span className="titleRow titleRow2">{titleR2}</span>
                <span className="titleRow titleRow3">
                    <span className="tokensWord" >{tokensWord}</span>
                    {titleR3}
                </span>
            </OverviewTitle>
            <OverviewContent className={`${animationStat} `} >{content}</OverviewContent>
            <UserActions animationStat={animationStat} />
        </AppOverviewContainer>
    );
}

export default AppOverview;