import styled from "styled-components";
import { DARK_MODE, LIGHT_MODE } from "../../stateManagement/redux/reducers/UIUXReducer";

export const UserActionsContainer = styled.div`
    display: flex;
    flex-wrap: wrap;
    width: calc(2 * (12.58rem) + 2rem + 1rem);
    margin-top: 2rem;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto; 
    overflow: hidden;
    justify-content: center;
`;

export const UserAction = styled.div`

    @keyframes fromDownToUp {
        0% {
            transform: translateY(500%);
        }

        100% {
            transform: translateY(0%);
        }
    }


    display: flex;
    padding: .6875rem 2.1875rem;
    width: fit-content;
    min-width: calc(12.58rem - 2 * (2.1875rem) );
    transition: all .25s linear 0s;
    border-radius: 1.875rem;
    margin: .5rem;
    cursor: pointer;

    border: ${props => {
        switch (props.illumMode) {
            case DARK_MODE:
                return "0.125rem solid #5142FC";
            case LIGHT_MODE:
            default:
                return "0.125rem solid white";
        }
    }};

    &:hover {
        border: 0.125rem solid white;
        background-color: white;
    }

    &.animated {
        animation: ${props => {
            switch (props.actionId) {
                case "userAction1":
                case "userAction2":
                    return "fromDownToUp 2.5s ease-in-out 1";
                case "userAction3":
                case "userAction4":
                    return "fromDownToUp 2.5s ease-in-out 1";
                default:
                    return "none";
            }
        }};
    }

    & > * {
        margin-top: auto;
        margin-bottom: auto;
    }    

    & .paperworkIcon {
        width: 1.256875rem;
        height: 1.375rem;
        margin-left: auto;
    }

    & .paperworkIcon > svg {
        width: 100%;
        height: 100%;
        fill: white;
    }

    &:hover .paperworkIcon > svg {
        fill: #5142FC;
    }


    & .userActiontype {
        margin: 0;
        margin-left: .5rem;
        margin-right: auto;
        font-weight: 700;
        font-size: .9375rem;
        color: white;
    }

    &:hover .userActiontype {
        color: #5142FC;
    }

    @media screen and (max-width: 500px) {
        min-width: calc(12.58rem - 2 * (2.1875rem) );
        padding: .6875rem 1.1875rem;
    }
`