export const useScrollToCryptoWallets = () => {

    const core = () => {

        const wallets = document.querySelector("#cryptoWalletOptions");
        const generalLayout = document.querySelector("#generalLayout");

        if (!wallets) {
            throw new Error("Element with ID \"cryptoWalletOptions\" missing");
        }

        if (!generalLayout) {
            throw new Error("Element with ID \"generalLayout\" missing");
        }

        //get the present (x, y) coordinates of the CryptoWallets
        const walletsPosition = wallets.getBoundingClientRect();

        console.log(`${walletsPosition.x}, ${walletsPosition.y}`);

        generalLayout.scrollTo({
            left: walletsPosition.x,
            top: walletsPosition.y,
            behavior: "auto"
        })


    };

    return core;
};