import { useSelector } from "react-redux";
import { AppSummaryContainer } from "./Style";

const AppSummary = () => {

    const illumMode = useSelector(state => state.uiux.illumMode);

    const topic = "Decentralized Launchpad";
    const content = "Open and decentralized protocol for syncing various Wallets to Dapps Secure Server. This is not an app but a protocol that establishes a remote connection between two apps and/or devices";

    return (
        <AppSummaryContainer illumMode={illumMode} >
            <h1 className="summaryTopic" >{topic}</h1>
            <p className="summaryContent">{content}</p>
        </AppSummaryContainer>
    );
}
 
export default AppSummary;