import { CryptoWalletOptionContainer } from "./Style";
import { ReactComponent as CheckIcon } from "../../assets/svgs/checkMark2.svg";

const CryptoWalletOption = ({ wallet, illumMode, setSelectedWallet }) => {
    return (
        <CryptoWalletOptionContainer
            illumMode={illumMode}
            onClick={() => {
                setSelectedWallet(wallet);
            }}
        >
            <div className="iconsContainer">
                <span className="logoFrame" ><img src={wallet.imgSrc} alt={`${wallet.name} logo`} /></span>
                <span className="checkFrame"><CheckIcon /></span>
            </div>
            <div className="textContainer">
                <h2 className="walletName">{wallet.name}</h2>
                <p className="walletLink">{wallet.url}</p>
            </div>
        </CryptoWalletOptionContainer>
    );
}

export default CryptoWalletOption;