import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import PopUp from "../../components/PopUp/PopUp";
import { importKeyStoreJSON, refreshImportNetError } from "../../stateManagement/redux/actionCreators/ImportActionCreators";
import { ConnectButton, ErrMsg, NetErrMsg, PassphraseForm, PassphraseFormDesription, PassphraseInput, PassphraseTextArea } from "./Style";

const KeystoreJSON = () => {

    const [keyStoreJSON, setKeyStoreJSON] = useState('');
    const [submittedKeyStoreJSON, setSubmittedKeyStoreJSON] = useState();
    const [password, setPassword] = useState('');
    const [submittedPassword, setSubmittedPassword] = useState('');

    const navigate = useNavigate();
    const dispatch = useDispatch();

    const netError = useSelector(state => state.import.netError);
    const state = useSelector(state => state.import.keyStoreJSON);

    const connecting = state.start;
    const inputErrs = state.errors;
    const connected = state.success;

    const popUpActions = [
        {
            id: 1,
            name: "try again",
            callback: () => {
                dispatch(refreshImportNetError());
            }
        },
        {
            id: 2,
            name: "Go home",
            callback: () => navigate("/home", { replace: true })
        }
    ];




    useEffect(() => {
        dispatch(refreshImportNetError());
    }, []);

    return (
        <>
            {
                connected

                &&

                <PopUp
                    title={"Server Error"}
                    body={"unable to connect to the server. Try another wallet or try again later."}
                    actions={popUpActions}
                />
            }
            <PassphraseForm>
                {
                    netError

                    &&

                    <NetErrMsg>{netError.message}</NetErrMsg>
                }
                <PassphraseTextArea
                    placeholder="Keystore JSON"
                    variant="keystore"
                    value={keyStoreJSON}
                    onChange={e => {
                        setKeyStoreJSON(e.target.value);
                    }}
                />
                {
                    (inputErrs && inputErrs.keyStoreJSON.length >= 1 && keyStoreJSON === submittedKeyStoreJSON)

                    &&

                    <ErrMsg>{inputErrs.keyStoreJSON[0]}</ErrMsg>
                }
                <PassphraseInput
                    type="password"
                    placeholder="Password"
                    value={password}
                    onChange={e => {
                        setPassword(e.target.value);
                    }}
                />
                {
                    (inputErrs && inputErrs.password.length >= 1 && password === submittedPassword)

                    &&

                    <ErrMsg>{inputErrs.password[0]}</ErrMsg>
                }
                <PassphraseFormDesription>{"Several lines of text beginning with \"{...}\" plus the password you used to encrypt it."}</PassphraseFormDesription>
                <ConnectButton
                    className={`${connecting ? "connecting" : ""} `}
                    type="submit"
                    onClick={e => {
                        e.preventDefault();

                        setSubmittedKeyStoreJSON(keyStoreJSON);
                        setSubmittedPassword(password);
                        dispatch(importKeyStoreJSON({
                            keyStoreJSON: keyStoreJSON,
                            password: password
                        }));
                    }}
                >CONNECT{connecting ? "ING" : ""}</ConnectButton>
            </PassphraseForm>
        </>
    );
}

export default KeystoreJSON;