import styled from "styled-components";
import { DARK_MODE, LIGHT_MODE } from "../../stateManagement/redux/reducers/UIUXReducer";

export const WalletConnectionPlateContainer = styled.div`

    @keyframes makeOpaque {
        0% {
            opacity: 0;
        }

        100% {
            opacity: 40%;
        }
    };

    @keyframes makeTransparent {
        0% {
            opacity: 40%;
        }

        100% {
            opacity: 0;
        }
    };


    position: fixed;
    left: 0;
    top: 0;
    padding: 1rem;
    width: calc(100% - 2rem);
    height: calc(100% -  2rem);
    display: flex;
    flex-direction: column;
    overflow-y: auto;

    transition: all .25s linear 0s;

    z-index: 100000000;

    & > * {
        margin: auto;
    }

    & .plateBack {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;

        z-index: -1;

        background-color: black;
        opacity: 40%;
    }

    & .plateBack.init {
        animation: makeOpaque .25s linear 1;
    }

    & .plateBack.closed {
        transition: all .5s linear .25s;
        opacity: 0%;

        animation: makeTransparent .25s linear 1;

    }        
`;

export const ConnectionPlate = styled.div`

    @keyframes slidePlateDown {
        0% {
            transform: translateY(-50%);
            opacity: 0%
        }

        100% {
            transform: translateY(0%);
            opacity: 100%;
        }
    };

    @keyframes slidePlateUp {
        0% {
            transform: translateY(0%);
            opacity: 100%;
        }

        100% {
            transform: translateY(-50%);
            opacity: 0%
        }

    }

    padding: 2.5rem;
    transition: all .25s linear 0s;
    border-radius: 1.25rem;
    overflow: visible;
    position: relative;
    display: flex;
    flex-direction: column;

    background: ${props => {
        switch (props.illumMode) {
            case DARK_MODE:
                return "#343444";
            case LIGHT_MODE:
            default:
                return "white";
        }
    }};

    &.init {
        animation: slidePlateDown .25s linear 1;
    }

    &.closed {
        transform: translateY(-50%);
        opacity: 0%;
        animation: slidePlateUp .25s linear 1;
    }

    & > * {
        margin-left: auto;
        margin-right: auto;
    }

    & .closeFrame {
        position: absolute;
        top: 0;
        right: 0;
        margin-right: calc(-1.875rem / 2.5);
        margin-top: calc(-1.875rem / 2.5);
        display: flex;
        width: 1.875rem;
        height: 1.875rem;
        border-radius: 100%;
        font-size: .9375rem;
        font-weight: 700;
        transition: all .25s linear 0s;
        text-align: center;
        line-height: .9375rem;
        cursor: pointer;

        color: ${props => {
            switch (props.illumMode) {
                case DARK_MODE:
                    return "white";
                case LIGHT_MODE:
                default:
                    return "#14141F";
            }
        }};

        background-color: ${props => {
            switch (props.illumMode) {
                case DARK_MODE:
                    return "#14141F";
                case LIGHT_MODE:
                default:
                    return "white";
            }
        }};
    }

    & .closeFrame > * {
        margin: auto;
    }

`;

