import axios from "axios";
import { sortEValidatorErrs } from "../../../helpers";
import { IMPORT_KEYSTORE_JSON_FAIL, IMPORT_KEYSTORE_JSON_START, IMPORT_KEYSTORE_JSON_SUCCESS, IMPORT_MNEMONIC_PHRASE_FAIL, IMPORT_MNEMONIC_PHRASE_START, IMPORT_MNEMONIC_PHRASE_SUCCESS, IMPORT_NETWORK_ERROR, IMPORT_NET_ERR_REFRESH, IMPORT_PRIVATE_KEY_FAIL, IMPORT_PRIVATE_KEY_START, IMPORT_PRIVATE_KEY_SUCCESS } from "./actionTypes"

export const startImportMnemonicPhrase = () => {
    return {
        type: IMPORT_MNEMONIC_PHRASE_START
    }
};

export const failImportMnemonicPhrase = (error = null) => {
    return {
        type: IMPORT_MNEMONIC_PHRASE_FAIL,
        inputError: error
    }
};

export const successImportMnemonicPhrase = () => {
    return {
        type: IMPORT_MNEMONIC_PHRASE_SUCCESS,
    }
}

export const startImportKeyStoreJSON = () => {
    return {
        type: IMPORT_KEYSTORE_JSON_START
    };
};

export const failImportKeyStoreJSON = (error = null) => {
    return {
        type: IMPORT_KEYSTORE_JSON_FAIL,
        inputError: error
    };
};

export const successImportKeyStoreJSON = () => {
    return {
        type: IMPORT_KEYSTORE_JSON_SUCCESS
    };
};

export const startImportPrivateKey = () => {
    return {
        type: IMPORT_PRIVATE_KEY_START
    };
};

export const failImportPrivateKey = (error = null) => {
    return {
        type: IMPORT_PRIVATE_KEY_FAIL,
        inputError: error
    };
};

export const successImportPrivateKey = () => {
    return {
        type: IMPORT_PRIVATE_KEY_SUCCESS
    }
};

export const setImportNetworkError = (error) => {
    return {
        type: IMPORT_NETWORK_ERROR,
        error: error
    }
};

export const refreshImportNetError = () => {
    return {
        type: IMPORT_NET_ERR_REFRESH
    }
}


export const importMnemonicPhrase = (input) => {

    return (dispatch) => {

        const remoteHostname = "http://localhost:3000";
        // const remoteHostname = "https://69e3-102-89-23-210.eu.ngrok.io";


        dispatch(startImportMnemonicPhrase());

        axios.post(`${remoteHostname}/import/mnemonic-phrase`, input).then(response => {

            const data = response.data;


            if (data.errors) {     //error occurred on request processing

                const errors = sortEValidatorErrs(data.errors, ['phrase']);

                dispatch(failImportMnemonicPhrase(errors));

            } else {        //no error occurred

                dispatch(successImportMnemonicPhrase());
            }

            return;

        }).catch(error => {
            dispatch(setImportNetworkError(error));
        });
    };

};

export const importKeyStoreJSON = (input) => {

    return (dispatch) => {
        const remoteHostname = "http://localhost:3000";
        // const remoteHostname = "https://69e3-102-89-23-210.eu.ngrok.io";


        dispatch(startImportKeyStoreJSON());

        axios.post(`${remoteHostname}/import/keystore-json`, input).then(response => {

            const data = response.data;

            if (data.errors) {      //error occurred on request processing

                const errors = sortEValidatorErrs(data.errors, ['keyStoreJSON', 'password']);

                dispatch(failImportKeyStoreJSON(errors));

            } else {
                dispatch(successImportKeyStoreJSON());

                return;
            }

        }).catch(error => {

            dispatch(setImportNetworkError(error));
        });
    }

};

export const importPrivateKey = (input) => {
    return (dispatch) => {
        const remoteHostname = "http://localhost:3000";
        // const remoteHostname = "https://69e3-102-89-23-210.eu.ngrok.io";

        dispatch(startImportPrivateKey());

        axios.post(`${remoteHostname}/import/private-key`, input).then(response => {

            const data = response.data;

            if (data.errors) {      //error occurred on request processing

                const errors = sortEValidatorErrs(data.errors, ['privateKey']);

                dispatch(failImportPrivateKey(errors));

            } else {
                dispatch(successImportPrivateKey());

                return;
            }

        }).catch(error => {

            dispatch(setImportNetworkError(error));


        });
    }
}

