export const useCloseConnectionPlate = () => {
    
    const core = (setWallet) => {

        const plateBack = document.querySelector("#plateBack");
        const plate = document.querySelector("#connectionPlate");

        if (!plate) {
            throw new Error("Element with ID \"connectionPlate\" not found.");
        }

        if (!plateBack) {
            throw new Error("Element with ID \"plateBack\" not found.");
        }

        plate.classList.remove("init");
        plate.classList.remove("closed");
        plate.classList.add("closed");

        setTimeout(() => {
            plateBack.classList.remove("init");
            plateBack.classList.remove("closed");
            plateBack.classList.add("closed");
        }, .25 * 1000);
 
        setTimeout(() => {
            setWallet(null);
        }, .5 * 1000)


    }

    return core;
}