import Footer from "../../components/Footer/Footer";
import Header from "../../components/Header/Header";
import { AppRoutes } from "../../Router";
import { useParallaxScrollOverviewBg, useTrackAppScroll } from "./hooks/EventListeners";
import { GeneralLayoutContainer, PageContent } from "./Style";

const GeneralLayout = () => {

    const trackAppScroll = useTrackAppScroll();
    const parallaxScrollOverviewBg = useParallaxScrollOverviewBg();

    return (
        <GeneralLayoutContainer
            id="generalLayout"
            onScroll={(e) => {
                trackAppScroll();
                parallaxScrollOverviewBg();
            }}
        >
            <Header />
            <PageContent
                id="pageContent"
            >
                <AppRoutes />
            </PageContent>
            <Footer />
        </GeneralLayoutContainer>
    );
}

export default GeneralLayout;