import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fixHeaderThenSlideDown, pullUpHeaderThenUnfix } from "../../../stateManagement/redux/actionCreators/UIUXActionCreators";
import { HEADER_FIXED, HEADER_UNFIXED } from "../../../stateManagement/redux/reducers/UIUXReducer";

const monitorFixHeaderEvent = (header, pastCoord, dispatch, _event = null) => {
    const eventYCoord = -350;

    //get the present x,y coordinates of the Header
    const headerPos = header.getBoundingClientRect();
    const { x, y } = {
        x: headerPos.x,
        y: headerPos.y
    };

    if (y < eventYCoord) {      //it may be time
        if (y < pastCoord.y) {      //it is time

            //dispatch the event
            if (!header.dispatchEvent(_event)) {
                throw new Error("Could not dispatch a new Header-fixed event");
            }

            dispatch(fixHeaderThenSlideDown(header, 0));

        }
    }

    return {
        x: x,
        y: y
    };
}

const monitorUnfixHeaderEvent = (header, pageContent, pastCoord, dispatch, _event = null) => {
    const eventYCoord = -350;

    //get the ideal present x,y coordinates of the Header, if it were to be relative to the pageContent
    const headerPos = pageContent.getBoundingClientRect();
    const { x, y } = {
        x: headerPos.x,
        y: headerPos.y
    };

    if (y > eventYCoord) {      //it may be time
        if (y > pastCoord.y) {      //it is time

            if (!header.dispatchEvent(_event)) {
                throw new Error("Could not dispath a new Header-unfix event");
            }

            dispatch(pullUpHeaderThenUnfix(header, .25));

        }
    }

    return {
        x: x,
        y: y
    };
}

export const useTrackAppScroll = () => {
    const dispatch = useDispatch();
    const headerPosition = useSelector(state => state.uiux.headerPositon);
    const [headerPastCoord, setHeaderPastCoord] = useState({ x: 0, y: 0 });

    const fixHeaderEvent = new CustomEvent(HEADER_FIXED);
    const unfixHeaderEvent = new CustomEvent(HEADER_UNFIXED);

    const core = () => {
        const pageContent = document.querySelector("#pageContent");
        const header = document.querySelector("#layoutHeader");

        if (!pageContent) {
            throw new Error("Element with id \"pageContent\" missing");
        }
        if (!header) {
            throw new Error("Element with id \"layoutHeader\" missing");
        }

        if (headerPosition === HEADER_FIXED) {

            const newHeaderPastCoord = monitorUnfixHeaderEvent(header, pageContent, headerPastCoord, dispatch, unfixHeaderEvent);
            setHeaderPastCoord(newHeaderPastCoord);

        } else if (headerPosition === HEADER_UNFIXED) {

            const newHeaderPastCoord = monitorFixHeaderEvent(header, headerPastCoord, dispatch, fixHeaderEvent);
            setHeaderPastCoord(newHeaderPastCoord);

        }
    }

    return core;
};

export const useParallaxScrollOverviewBg = () => {

    const core = () => {
        const appOverviewBg = document.getElementById("appOverviewBg");
        const generalLayout = document.getElementById("generalLayout");

        if (!appOverviewBg) {
            throw new Error("Element with id \"appOverviewBg\" missing");
        }

        if (!generalLayout) {
            throw new Error("Element with id \"generalLayout\" missing");
        }

        const yCoord = appOverviewBg.getBoundingClientRect().y;
        const xValue = "center";
        const factor = .5;

        appOverviewBg.style.backgroundPosition = `${xValue} ${- (yCoord * factor)}px`;
    }

    return core;
}