import { useDispatch, useSelector } from "react-redux";
import { ToolBarContainer } from "./Style";
import { ReactComponent as WalletIcon } from "../../assets/svgs/Wallet.svg"
import { ReactComponent as SearchIcon } from "../../assets/svgs/Search.svg"
import { setLightMode, setDarkMode, toggleSearchBar } from "../../stateManagement/redux/actionCreators/UIUXActionCreators";
import { useScrollToCryptoWallets } from "./hooks/EventListeners";


const ToolBar = ({ className = "" }) => {
    const dispatch = useDispatch();
    const illumMode = useSelector(state => state.uiux.illumMode);
    const sunIconSrc = "/media/images/sun.png";
    const moonIconSrc = "/media/images/moon.png";
    const scrollToCryptoWallets = useScrollToCryptoWallets();

    return (
        <ToolBarContainer className={`${className}`} illumMode={illumMode} >
            <span
                className="toolIconFrame searchIconFrame"
                onClick={() => dispatch(toggleSearchBar())}
            ><SearchIcon /></span>
            <span
                className="walletValidationToolContainer"
                onClick={() => {
                    scrollToCryptoWallets();
                }}
            >
                <span className="toolIconFrame walletIconFrame"><WalletIcon /></span>
                <p className="toolName">Validate Wallet</p>
            </span>
            <span className="illumModeToolsContainer">
                <span
                    className="toolIconFrame sunIconFrame"
                    onClick={() => dispatch(setLightMode())}
                ><img src={sunIconSrc} alt="sun icon" /></span>
                <span
                    className="toolIconFrame moonIconFrame"
                    onClick={() => dispatch(setDarkMode())}
                ><img src={moonIconSrc} alt="moon icon" /></span>
            </span>
        </ToolBarContainer>
    );
}

export default ToolBar;