import { HeaderContainer } from "./Style";
import { useSelector } from "react-redux";
import ToolBar from "../ToolBar/ToolBar";
import { useNavigate } from "react-router-dom";
import SearchBar from "../SearchBar/SearchBar";

const Header = () => {

    const navigate = useNavigate();
    const illumMode = useSelector(state => state.uiux.illumMode);
    const headerPosition = useSelector(state => state.uiux.headerPositon);
    const appName = "Decentralized Launchpad";

    return (
        <HeaderContainer id="layoutHeader" illumMode={illumMode} headerPosition={headerPosition} >
            <div className="limiter">
                <div className="titleContainer">
                    <h1
                        id="title"
                        onClick={() => navigate("/", { replace: true })}
                    >{appName}</h1>
                </div>
                <ToolBar className="toolbar" />
                <div className="searchBarContainer">
                    <SearchBar />
                </div>
            </div>
        </HeaderContainer>
    );
}

export default Header;