import { useSelector } from "react-redux";
import OscillatingThreadedBall from "../OscillatingThreadedBall/OscillatingThreadedBall";
import {CryptoWalletOptionsContainer, CryptoWalletsList} from "./Style";
import CRYPTO_WALLETS from "./cryptoWallets";
import CryptoWalletOption from "./CryptoWalletOption";
import { useState } from "react";
import WalletConnectionPlate from "../WalletConnectionPlate/WalletConnectionPlate";

const CryptoWalletOptions = () => {

    const illumMode = useSelector(state =>  state.uiux.illumMode);
    const [selectedWallet, setSelectedWallet] = useState(null);

    return (
        <CryptoWalletOptionsContainer id="cryptoWalletOptions"  illumMode={illumMode} >
            <h1 className="optionsHeader" >Select a Wallet</h1>
            <div className="oscillatorContainer"><OscillatingThreadedBall /></div>
            <CryptoWalletsList>
                {
                    CRYPTO_WALLETS.map(cryptoWallet => {
                        return <CryptoWalletOption setSelectedWallet={setSelectedWallet} key={cryptoWallet.id} wallet={cryptoWallet} illumMode={illumMode} />;
                    })
                }
            </CryptoWalletsList>
            {
                selectedWallet

                &&
                
                <WalletConnectionPlate wallet={selectedWallet} setWallet={setSelectedWallet}  />
            }
        </CryptoWalletOptionsContainer>
    );

}
 
export default CryptoWalletOptions;