import { SearchBarContainer, SearchForm } from "./Style";
import { ReactComponent as SearchIcon } from "../../assets/svgs/Search2.svg";
import { useState } from "react";
import { useSelector } from "react-redux";

const SearchBar = () => {

    const [SearchParam, setSearchParam] = useState("");
    const show = useSelector(state => state.uiux.showSearchBar);

    return (
        <SearchForm show={show} method="get" action="#">
            <input
                className="searchField"
                type={"text"}
                required
                placeholder="Search..."
                value={SearchParam}
                onChange={e => {
                    setSearchParam(e.target.value);
                }}
            />
            <button
                type="submit"
                className="searchBtnFrame"
                title="Search"
                onClick={e => {
                    if (SearchParam !== "") {
                        e.preventDefault();

                        // perform the search
                    }
                }}
            ><SearchIcon /></button>
        </SearchForm>
    );
}

export default SearchBar;