import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setViewportLayout } from "../../stateManagement/redux/actionCreators/UIUXActionCreators";
import { GENERAL_LAYOUT, PASSPHRASE_LAYOUT } from "../../stateManagement/redux/reducers/UIUXReducer";
import { NotFoundContainer } from "./Style";

const NotFound = () => {

    const viewportLayout = useSelector(state => state.uiux.viewportLayout);
    const dispatch = useDispatch();
    const [isSet, setIsSet] = useState(false);

    useEffect(() => {

        if (!isSet) {

            setIsSet(true);

            if (viewportLayout === GENERAL_LAYOUT) {
                dispatch(setViewportLayout(PASSPHRASE_LAYOUT));
            } else {
                dispatch(setViewportLayout(GENERAL_LAYOUT));
            }
        }
        
    }, []);

    return (
        <NotFoundContainer>
            <h1 className="err404">Error 404</h1>
            <p className="notFound">Not found</p>
        </NotFoundContainer>
    );
}

export default NotFound;