
/**
 * if the passed number is odd, it returns the greatest odd whole-number lesser than it
 * @param {*} number 
 * @returns 
 */
export const getFloorOdd = (number) => {
    if (number % 2 === 0) {     //the passed number is even
        return ((number === 0) ? 0 : number - 1);
    }

    return number;    
}

/**
 * sorts Express-Validator errors
 * @param {*} errors 
 * @param {*} fields the fields of the inputs the errors refer to
 */
export const sortEValidatorErrs = (errors, fields) => {

    let sortedErrors = {};

    for (const field of fields) {
        sortedErrors[field] = [];
    }

    for (const error of errors) {

        if (!sortedErrors[error.param]) {
            sortedErrors[error.param] = [];
        }

        let errSet = sortedErrors[error.param];
        errSet[errSet.length] = error.msg;
    }

    return sortedErrors;
}
